import { Component } from '@angular/core';
import { LocalStorage } from '@ngx-pwa/local-storage';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.styl']
})
export class AppComponent {
  constructor(protected localStorage:LocalStorage) {}
  
  title = 'todo';
  newTodo = false;
  addNewTodo = function(){
    if(this.newTodo){
      this.todos.push({name: this.name, text: this.text})
      this.saveData();
      this.newTodo = false;
    } else { this.newTodo = true; }
  };
  chaining = false
  selectTodo = function(todo) {
    for(let i=0;i<this.todos.length;i++){
      if (this.todos[i]===todo){ this.todos.splice(i,1); this.saveData(); }
    }
  }
  saveData = function() {
    this.localStorage.setItem('todos', this.todos).subscribe(()=>{});
  }
  readData = function() {
    this.localStorage.getItem('todos').subscribe((todos) => {
      this.todos = todos;
    });
  }
  todos = this.readData();
  name = '';
  text = '';
  onNameKey = function(value: string){ this.name = value };
  onTextKey = function(value: string){ this.text = value };
}
